
html,body{
  margin: 0;
  overflow-y: hidden;
  height: 100%;
  width: 100%;
}































































































































































































































.card-modal-grid{
  display: grid;
  grid-template-columns: 70% auto;
  grid-template-rows: 28% auto;
}
.card-modal-header{
  grid-column-start: 1;
  grid-column-end: 3;
}
.card-modal-main{
  grid-column-start: 1;
  grid-column-end: 2;
}
.card-modal-sidebar{
  grid-column-start: 2;
  grid-column-end: 3;
}

.card-header-popover-header{
  text-align: center;
  border-bottom: 1px solid black;
}
